import React from 'react';
import ReactDOM from "react-dom/client";
import {Routes, Route, HashRouter, BrowserRouter } from "react-router-dom";
import * as serviceWorker from "./serviceWorker";
import {createTheme, ThemeProvider} from "@mui/material";
import CssBaseline from '@mui/material/CssBaseline';
import Clients from "./Components/Clients/Clients";
import RentalEquipment from "./Components/RentalEquipment/RentalEquipment";
import Employees from "./Components/Employees/Employees";

const darkTheme = createTheme({
    palette: {
        mode: 'dark',
    },
});


const renderReactDom = () => {
    const root = ReactDOM.createRoot(
        document.getElementById("root")
    );
    root.render(
        <React.StrictMode>
            <ThemeProvider theme={darkTheme}>
                <CssBaseline />
    {/*            <HashRouter>
                    <Routes>
                        <Route path={"/1"}>
                            <Employees/>
                        </Route>
                        <Route path="/employees" element={<Employees />} />
                        <Route path="/clients" element={<Clients />} />
                        <Route path="/rentalEquipment" element={<RentalEquipment />} />
                    </Routes>
                </HashRouter>*/}
                <HashRouter>
                    <Routes>
                        <Route exact path="/" element={<Clients />} />
                        <Route exact path="/2" element={<RentalEquipment />} />
                        <Route exact path="/3" element={<Employees />} />
                    </Routes>
                </HashRouter>
            </ThemeProvider>
        </React.StrictMode>
    );
};

if (window.cordova) {
  document.addEventListener('deviceready', () => {
    renderReactDom();
  }, false);
} else {
  renderReactDom();
}
serviceWorker.unregister();